.section {
  padding: 5rem 1.5rem 6rem;
  background: radial-gradient(circle, rgba(20, 183, 255, 0.12), rgba(1, 8, 20, 1));
  color: #f1f6ff;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  gap: 2.5rem;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  align-items: center;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.4em;
  font-size: 0.7rem;
  color: #14b7ff;
}

.copy {
  margin: 1rem 0;
  color: #b0c6ff;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.list li + li {
  margin-top: 0.5rem;
}

.list a {
  color: #ffe066;
  text-decoration: none;
}

.form {
  display: grid;
  gap: 0.85rem;
}

.form input,
.form textarea {
  border-radius: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0.85rem 1rem;
  background: rgba(3, 19, 42, 0.8);
  color: #f1f6ff;
  resize: none;
}

.form button {
  background: linear-gradient(135deg, #ffe066, #14b7ff);
  border: none;
  padding: 0.85rem 1rem;
  border-radius: 999px;
  font-weight: 700;
  cursor: pointer;
  color: #010814;
}

