.section {
  padding: 5rem 1.5rem;
  background: #010814;
  color: #f5f7ff;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  text-align: center;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.3em;
  font-size: 0.75rem;
  color: #7dd3ff;
}

.grid {
  margin-top: 2.5rem;
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

.card {
  background: rgba(3, 19, 42, 0.9);
  border: 1px solid rgba(125, 211, 255, 0.2);
  border-radius: 1rem;
  padding: 1.75rem;
  text-align: left;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.35);
}

.card h3 {
  margin-bottom: 0.5rem;
}

.card p {
  color: #a7c4ff;
  min-height: 60px;
}

.card button {
  margin-top: 1rem;
  background: linear-gradient(135deg, #ffe066, #14b7ff);
  border: none;
  padding: 0.6rem 1.5rem;
  border-radius: 999px;
  font-weight: 600;
  cursor: pointer;
  color: #010814;
}

