.section {
  padding: 5rem 1.5rem;
  background: #020d1f;
  color: #f5f7ff;
}

.container {
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.4em;
  font-size: 0.7rem;
  color: #ffe066;
}

.copy {
  margin-top: 1rem;
  color: #a7c4ff;
  line-height: 1.8;
}

.stats {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  flex-wrap: wrap;
}

.stats strong {
  display: block;
  font-size: 2rem;
}

.stats span {
  color: #a7c4ff;
  font-size: 0.9rem;
}

